*{
  font-family: 'Ubuntu', sans-serif;
}
button {
  color: #FFFFFF;
  border: none;
  border-radius: 5px;
  padding: 7px 10px;
  background-color: #085470;
  box-shadow: 0 10px 17px #949292 ;
  margin: 0 10px;
  cursor: pointer;
}
button:active{
  transform: scale(90%);
  box-shadow: 0 10px 25px #949292 ;
}
p {
  color: #0f6f92;
  text-shadow: 3px 4px 7px rgba(81, 67, 21, 0.8);
}
h1{
  color: #f9f9f5;
  text-shadow: 3px 4px 7px rgba(81, 67, 21, 0.8);
}
input{
  padding: 7px 15px;
  border-radius: 10px;
}
.container{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.inputs{
  
}
input{
  display: block;
  margin: 10px;
}
.container .content{
  padding: 10px;
  width: auto;
  height: auto;
  background-color: #6284ff42;
  border-radius: 10px;
  box-shadow: 0 10px 20px #767474;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 50px;
  transition: height 0.5s linear;
}
.combobox{
  display: flex;
  align-items: center;
}
.combobox select{
  padding: 8px 15px;
  border-radius: 10px;
  border: 1px solid #1191ab;
}
.matrix_line div{
  display: flex;
}

.Item div{
  display: flex;
  align-items: center;
  justify-content: center;
}

select{
  padding: 5px 10px;
  border-radius: 10px;
  border-image: linear-gradient(to right, red, blue);
  
}